import { Component, OnInit } from '@angular/core';
import { DirektoriPermintaanPegawaiService, CompanyDepartemenService, CompanyJabatanService, CompanyPangkatService, CompanyKantorService, CompanyDivisiService } from 'src/app/sdk/hr/services';
import { NzNotificationService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Permintaan_pegawai, Departemen, Jabatan, Pangkat, Kantor, Divisi } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';


@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(

        private direktoriPermintaanPegawaiService: DirektoriPermintaanPegawaiService,
        private notification: NzNotificationService,

        private companyDepartemenService: CompanyDepartemenService,
        private companyJabatanService: CompanyJabatanService,
        private companyPangkatService: CompanyPangkatService,
        private companyDivisiService: CompanyDivisiService,
        private companyKantorService: CompanyKantorService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private activatedRoute: ActivatedRoute,
        private pesan: PesanService,
        private router: Router,
        private singleUploadService: SingleUploadService,
    ) { }



    dataPermintaanPegawai: Permintaan_pegawai = {}

    id_permintaan_pegawai: string = '';

    listDepartemen: Departemen[] = []
    listJabatan: Jabatan[] = []
    listPangkat: Pangkat[] = []
    listKantor: Kantor[] = []
    listDivisi: Divisi[] = []

    toogleUpload = false;
    ngOnInit() {
        this.id_permintaan_pegawai = this.activatedRoute.snapshot.paramMap.get('id_permintaan_pegawai')
        this.form()
        this.getPreeData()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'employee_managejob.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    getPreeData() {

        this.companyPangkatService.getCompanyPangkats().subscribe(
            data => {
                this.listPangkat = data.data;
            }
        )
        this.companyKantorService.getCompanyKantors().subscribe(
            data => {
                this.listKantor = data.data;
            }
        )

        this.companyDepartemenService.getCompanyDepartemens().subscribe(
            data => {
                this.listDepartemen = data.data;
            }
        )
    }

    getDivisi() {
        this.spinner.show()
        this.companyDivisiService.getCompanyDivisis(JSON.stringify(
            {
                id_departemen: this.dataForm.value.permintaan_departemen,
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listDivisi = data.data;
                }
            )
    }

    getJabatan() {
        this.spinner.show()
        this.companyJabatanService.getCompanyJabatans(JSON.stringify(
            {
                id_divisi: this.dataForm.value.permintaan_divisi,
            })).subscribe(
                data => {
                    this.spinner.hide();
                    this.listJabatan = data.data;
                    if(data.data.length < 1)
                    {
                        this.pesan.pesanWarningForm('data jabatan kosong!')
                    }
                }
            )

    }

    getData() {
        this.spinner.show()
        this.direktoriPermintaanPegawaiService.getDirektoriPermintaanPegawaisId(this.id_permintaan_pegawai).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "employee_managejob",
                    for_table: "erp_hr.permintaan_pegawai",
                    for_module_id: data.data.id_permintaan_pegawai
                })
            }
        )
    }


    dataForm: FormGroupTypeSafe<Permintaan_pegawai>
    form() {
        this.dataForm = this.fb.group<Permintaan_pegawai>({
            permintaan_jabatan: new FormControl('', [Validators.required]),
            permintaan_departemen: new FormControl(''),
            permintaan_pangkat: new FormControl(''),
            lokasi_kerja: new FormControl(''),
            type_jam_kerja: new FormControl(''),
            tanggal_dibutuhkan: new FormControl(''),
            jumlah_kebutuhan: new FormControl(''),
            Rencana: new FormControl(''),
            tidak_sesuai_rencana: new FormControl(''),
            status_hubungan_kerja: new FormControl(''),
            id_jabatan_atasan: new FormControl(''),
            tugas_utama: new FormControl(''),
            pendidikan_minimum: new FormControl(''),
            jumlah_bawahan: new FormControl(''),
            pengalaman_minimal: new FormControl(''),
            spesifik_pengalaman: new FormControl(''),
            sertifikat_dibutuhkan: new FormControl(''),
            keterampilan_dibutuhkan: new FormControl(''),
            ruang_kerja: new FormControl(''),
            umur: new FormControl(''),
            jenis_kelamin: new FormControl(''),
            tinggi: new FormControl(''),
            berat: new FormControl(''),
            status_perkawinan: new FormControl(''),
            berkacamata: new FormControl(''),
            gaji_pokok: new FormControl(''),
            uang_makan_transport: new FormControl(''),
            uang_jabatan: new FormControl(''),
            uang_tunjangan: new FormControl(''),
            lembur: new FormControl(''),
            jamsostek: new FormControl(''),
            fasilitas_lain: new FormControl(''),
            id_user: new FormControl(''),
            id_permintaan_pegawai: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_data_flow_detail: new FormControl(''),
            id_data_flow: new FormControl(''),
            status: new FormControl(''),
            posisi: new FormControl(''),
            nomor: new FormControl(''),
            status_permintaan: new FormControl(''),
            id_dokumen: new FormControl(''),
            permintaan_divisi: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPermintaanPegawai = this.dataForm.value
        this.direktoriPermintaanPegawaiService
            .putDirektoriPermintaanPegawaisId({ id: this.id_permintaan_pegawai, body: this.dataPermintaanPegawai })
            .subscribe(
                data => {

                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }



    goToBack() {

        this.router.navigate(['/hr/direktori/permintaan-pegawai/list'])
    }

}
