import { Jabatan, Departemen, Pangkat, Pelamar } from 'src/app/sdk/hr/models';
import { Permintaan_pegawai } from 'src/app/sdk/hr/models/permintaan-_pegawai';
import { NgxSpinnerModule, NgxSpinnerService } from 'ngx-spinner';
import { CompanyPangkatService, CompanyJabatanService, CompanyDepartemenService, DirektoriPermintaanPegawaiService, DirektoriPelamarService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { StandarApprovalService, standarFlow } from 'src/app/components/approval/standar/standar-approval.service';
import { ToolsLogFlowService } from 'src/app/sdk/user/services';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private companyPangkatService: CompanyPangkatService,
        private companyJabatanService: CompanyJabatanService,
        private companyDepartemenService: CompanyDepartemenService,
        private direktoriPermintaanPegawaiService: DirektoriPermintaanPegawaiService,
        private direktoriPelamarService: DirektoriPelamarService,
        private routerActive: ActivatedRoute,
        private spinner: NgxSpinnerService,
        private router: Router,
        private standarApprovalService: StandarApprovalService,
        private toolsLogFlowService: ToolsLogFlowService
    ) { }

    id_permintaan_pegawai: string = ''
    PermintaanPegawai: Permintaan_pegawai = {}
    dataJabatan: Jabatan = {}
    dataDepartemen: Departemen = {}
    dataPangkat: any = {}

    pelamar: Pelamar[] = []
    kandidat: Pelamar[] = []
    ngOnInit() {
        this.routerActive.paramMap.subscribe(
            params => {
                if (params.get('id_permintaan_pegawai')) {
                    this.id_permintaan_pegawai = params.get('id_permintaan_pegawai')
                }
                this.getPermintaanPegawai()
                this.getLog()
            }
        )

        this.standarApprovalService.isreload.next(false);
        this.standarApprovalService.getisreload().subscribe(
            data => {
                if (data) {
                    this.getPermintaanPegawai()
                    this.getLog()
                }
            }
        )



    }

    params: standarFlow = null;
    id_data_flow: string = ''

    getPermintaanPegawai() {
        this.spinner.show()
        this.direktoriPermintaanPegawaiService.getDirektoriPermintaanPegawaisId(this.id_permintaan_pegawai).subscribe(
            data => {
                this.PermintaanPegawai = data.data;
                this.getJabatan(data.data.permintaan_jabatan)
                this.getDepartemen(data.data.permintaan_departemen)
                this.getPangkat(data.data.permintaan_pangkat)

                this.params = {
                    id: data.data.id_permintaan_pegawai,
                    id_data_flow_detail: data.data.id_data_flow_detail,
                    id_data_flow: data.data.id_data_flow,
                    next_status: null,
                    primary_key: "id_permintaan_pegawai"
                }
                this.standarApprovalService.param.next(this.params)
                this.spinner.hide()
            }
        )

        this.direktoriPelamarService.getDirektoriPelamars(JSON.stringify({ id_permintaan_pegawai: this.id_permintaan_pegawai })).subscribe(
            data => {
                this.pelamar = data.data
            }
        )

        this.direktoriPelamarService.getDirektoriPelamars(JSON.stringify({ status: 'dipertimbangkan' })).subscribe(
            data => {
                this.kandidat = data.data
            }
        )

    }

    getJabatan(permintaan_jabatan) {
        this.companyJabatanService.getCompanyJabatansId(permintaan_jabatan).subscribe(
            data => {
                this.dataJabatan = data.data
            }
        )
    }
    getDepartemen(permintaan_departemen) {
        this.companyDepartemenService.getCompanyDepartemensId(permintaan_departemen).subscribe(
            data => {
                this.dataDepartemen = data.data
            }
        )
    }
    getPangkat(permintaan_pangkat) {
        this.companyPangkatService.getCompanyPangkatsId(permintaan_pangkat).subscribe(
            data => {
                this.dataPangkat = data.data
            }
        )
    }

    listLog = [];
    getLog() {
        this.spinner.show()
        this.toolsLogFlowService.getToolsLogFlows(JSON.stringify({ for_module_id: this.id_permintaan_pegawai })).subscribe(
            data => {
                this.listLog = data.data;
                this.spinner.hide()
            }
        )
    }


    goToBack() {
        this.router.navigate(['/hr/direktori/permintaan-pegawai/list'])
    }


    //permintaan
    pelamarTerima(id_pelamar) {

    }

}
